import * as yup from 'yup';

yup.addMethod( yup.string, 'is', function ( expectedValue ) {
	return this.test( `test-equals`, null, ( valueToValidate ) => valueToValidate === expectedValue );
} );

yup.addMethod( yup.string, 'isNot', function ( expectedValue ) {
	return this.test( `test-not-equals`, null, ( valueToValidate ) => valueToValidate !== expectedValue );
} );

yup.addMethod( yup.array, 'has', function ( expectedValue ) {
	return this.test( `test-equals`, null, ( valueToValidate ) => valueToValidate.includes( expectedValue ) );
} );

const validate = ( rules, value, isNumeric ) => {
	if ( isNumeric ) {
		value = parseFloat( value );
	} else if ( !value ) {
		value = '';
	}

	let result = [];

	( Array.isArray( rules ) ? rules : [ rules ] ).every( data => {
		const { rule, message = '' } = data;

		if ( !rule ) {
			return true;
		}

		let _yup = isNumeric ? yup.number() : yup.string();

		if ( Array.isArray( value ) ) {
			_yup = yup.array();
		}

		let [ method, ...param ] = rule.split( ':' );
		param = param.join( ':' );

		try {
			_yup = _yup[ method ]( param );

			if ( !( _yup ).isValidSync( value ) ) {
				result.push( message );

				if ( rule === 'required' ) {
					// Break as soon as the required field fails.
					return false;
				}
			}
		} catch ( e ) {
			result.push( `Invalid validation rule: "${ rule }"` );
		}

		return true;
	} );

	return result;
};

const compareValues = ( first, second, op ) => {
	switch ( op ) {
		case '!=':
			return first != second;
		case '>':
			return parseInt( first, 10 ) > parseInt( second, 10 );
		case '<':
			return parseInt( first, 10 ) < parseInt( second, 10 );
		case 'pattern':
			return new RegExp( first, 'i' ).test( second );
		case '=':
		default:
			return first == second;
	}
};

export { validate, compareValues, yup };