<script>
	import { ajaxRequest, sanitizeAndProcessMarkdown } from '@lib/helpers';

	export let actionLabel;
	export let doingActionLabel;
	export let payload;
	export let _wpAjaxUrl = '';
	export let _wpAjaxAction;
	export let _wpNonce;
	export let ajaxRouter;
	export let ajaxRoute;
	export let disabled;
	export let showActionResult = true;
	export let beforeStart;
	export let onFinish;
	export let buttonProperties;

	let serverAction = false;
	let serverActionError = undefined;

	/**
	 * Saves settings in the backend.
	 */
	function doAjaxAction() {
		serverAction = true;
		serverActionError = undefined;

		if ( beforeStart ) {
			beforeStart();
		}

		const request = {
			wpAjaxUrl: _wpAjaxUrl,
			wpNonce: _wpNonce,
			wpAjaxAction: _wpAjaxAction,
			ajaxRouter,
			ajaxRoute,
			payload
		};

		ajaxRequest( request ).then( ( { success, response } ) => {
			serverAction = false;

			if ( !success ) {
				serverActionError = response;
			}

			if ( onFinish ) {
				onFinish( success, response );
			}
		} );
	}
</script>

{#if serverActionError && showActionResult}
	<div class="mb-4 rounded-md {serverActionError ? 'bg-red-50' : 'bg-green-50'} p-4">
		<div class="flex">
			<div class="flex-shrink-0">
				{#if serverActionError}
					<svg class="h-5 w-5 text-red-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
						<path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd" />
					</svg>
				{:else}
					<svg class="h-5 w-5 text-green-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
						<path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clip-rule="evenodd" />
					</svg>
				{/if}
			</div>
			<div class="ml-3">
				<slot name="response">
					<p class="text-sm font-medium {serverActionError ? 'text-red-700' : 'text-green-700'}">
						{@html sanitizeAndProcessMarkdown( serverActionError )}
					</p>
				</slot>
			</div>
			<div class="ml-auto pl-3">
				<div class="-mx-1.5 -my-1.5">
					<button
						type="button"
						class="inline-flex {serverActionError ? 'bg-red-50 text-red-500 hover:bg-red-100 focus:ring-offset-red-50 focus:ring-red-600' : 'bg-green-50 text-green-500 hover:bg-green-100 focus:ring-offset-green-50 focus:ring-green-600'} rounded-md p-1.5 focus:outline-none focus:ring-2 focus:ring-offset-2"
						on:click={() => showActionResult = undefined}
					>
						<span class="sr-only">Dismiss</span>
						<svg class="h-5 w-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
							<path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd" />
						</svg>
					</button>
				</div>
			</div>
		</div>
	</div>
{/if}

<button
	type="submit"
	disabled="{serverAction || disabled}"
	class="gk-button-primary"
	{...buttonProperties}
	on:click={doAjaxAction}
>
	{#if !serverAction}
		{actionLabel}
	{:else}
		<svg class="motion-safe:animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
			<circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
			<path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
		</svg>
		{doingActionLabel}
	{/if}
</button>
