<script>
	import { onMount, afterUpdate } from 'svelte';
	import * as validation from '@lib/validation';
	import { sanitizeAndProcessMarkdown } from '@lib/helpers';

	export let setting = {};
	export let settings = [];
	export let onChange;

	const { __ } = window.gkSettings;

	let isValid = true;
	let isVisible = true;
	let validationResults = [];

	let isInitialized = false;

	if ( setting.requires && !Array.isArray( setting.requires ) ) {
		setting.requires = [ setting.requires ];
	}

	/**
	 * Handles changes to the setting.
	 *
	 * @since 1.0
	 *
	 * @param {string|boolean|number} newValue
	 */
	function handleChange( newValue ) {
		if ( setting.excludeFromSave || setting.value === newValue ) {
			return;
		}

		if ( setting.type === 'checkbox' ) {
			setting.value = ~~newValue;
		} else if ( setting.type === 'multiple_checkboxes' ) {
			const checkbox_values = new Set( setting.value );

			if ( checkbox_values.has( newValue ) ) {
				checkbox_values.delete( newValue );
			} else {
				checkbox_values.add( newValue );
			}

			setting.value = Array.from( checkbox_values );
		} else {
			setting.value = newValue;
		}

		toggleVisibility();
	}

	function selectAllCheckboxes( checked ) {
		if ( checked ) {
			setting.value = [];
			setting.choices.forEach( choice => {
				setting.value.push( choice.value );
			} );
		} else {
			setting.value = [];
		}
	}

	/**
	 * Optionally validates the setting.
	 *
	 * @since 1.0
	 */
	function validate() {
		if ( !setting.validation || !isVisible ) {
			isValid = true;

			return;
		}

		let results = validation.validate( setting.validation, setting.value, setting.type === 'number' );

		validationResults = results.map( result => result.replace( /%value%/, setting.value ).replace( /%setting%/, setting.title ) );

		isValid = !validationResults.length;
	}

	/**
	 * Checks if the setting should be hidden based on dependency on other setting's value.
	 *
	 * @since 1.0
	 */
	function toggleVisibility() {
		if ( !setting.requires ) {
			isVisible = true;

			return;
		}

		const isRequirementMet = ( reqId, reqValue, reqOperator ) => {
			if ( !reqId || ( settings.hasOwnProperty( reqId ) && !reqValue ) ) {
				return true;
			}

			if ( !settings.hasOwnProperty( reqId ) ) {
				return false;
			}

			return validation.compareValues( reqValue, settings[ reqId ], reqOperator );
		};

		if ( !Array.isArray( setting.requires ) ) {
			setting.requires = [ setting.requires ];
		}

		isVisible = !setting.requires.filter( req => !isRequirementMet( req.id, req.value, req.operator ) ).length;
	}

	onMount( () => {
		if ( setting.type === 'multiple_checkboxes' && !Array.isArray( setting.value ) ) {
			setting.value = [];
		}

		handleChange( setting.value || setting?.choices?.[ 0 ].value || null );

		isInitialized = true;
	} );

	afterUpdate( () => {
		toggleVisibility();

		validate();

		if ( onChange ) {
			onChange( { ...setting, isValid, isVisible } );
		}
	} );
</script>

{#if isVisible && isInitialized}
	<div>
		<!-- Static HTML content -->
		{#if setting.html}
			<div class="" tabindex="0">
				{@html sanitizeAndProcessMarkdown( setting.html, setting.unsafe, setting.markdown )}
			</div>
		{:else}
			<div class="setting">
				<div class="flex flex-col space-y-3 w-4/5 sm:w-2/5">
					<p id="{setting.id}-label" class="text-sm font-medium text-gray-900">
						<label for="{setting.id}-input" class="cursor-pointer">
							{@html sanitizeAndProcessMarkdown( setting.title )}
							{#if setting.required}
                            <span class="text-red-500">
                                <i aria-hidden="true">*</i>
                                <i class="sr-only">{ __( 'Required', 'gk-foundation' )}</i>
                            </span>
							{/if}
						</label>
					</p>
					{#if setting.description}
						<p id="{setting.id}-description" class="text-sm text-gray-500">
							{@html sanitizeAndProcessMarkdown( setting.description, setting.unsafe, setting.markdown )}
						</p>
					{/if}
					{#if setting.link}
						<a
							href="{setting.link.url}"
							class="text-blue-gv text-sm underline"
							target="_blank"
							rel="noopener noreferrer"
							{...setting.link.properties}
						>
							{@html sanitizeAndProcessMarkdown( setting.link.title )}
						</a>
					{/if}
				</div>

				<!-- Select element -->
				{#if setting.type === 'select'}
					<div class="flex flex-col items-start space-y-3">
						<div class="relative w-full sm:w-56 items-start lg:w-72">
							<select
								id="{setting.id}-input"
								value="{setting.value || setting.choices[0].value}"
								aria-required="{setting.required ? 'true' : null}"
								aria-invalid="{!isValid ? 'true' : 'false'}"
								aria-describedby="{!isValid ? `${setting.id}-validation-error` : `${setting.id}-description`}"
								aria-labelledby="{setting.id}-label"
								class="{!isValid ? 'border-red-600 placeholder-red-600 focus:ring-gray-900 focus:border-gray-900 text-red-600' : 'text-gray-900 focus:ring-gray-900 focus:border-gray-900 border-gray-300'} block w-full text-sm p-3 rounded-md"
								on:change={(e) => handleChange( e.target.value )}
							>
								{#each setting.choices as choice}
									<option value={choice.value}>
										{@html sanitizeAndProcessMarkdown( choice.title )}
									</option>
								{/each}
							</select>
							{#if !isValid}
								<div class="absolute inset-y-0 right-6 pr-3 flex items-center pointer-events-none text-red-600">
									<svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
										<path d="M7.99967 5.33325V7.99992M7.99967 10.6666H8.00634M14.6663 7.99992C14.6663 11.6818 11.6816 14.6666 7.99967 14.6666C4.31778 14.6666 1.33301 11.6818 1.33301 7.99992C1.33301 4.31802 4.31778 1.33325 7.99967 1.33325C11.6816 1.33325 14.6663 4.31802 14.6663 7.99992Z" stroke="currentColor" stroke-width="1.33333" stroke-linecap="round" stroke-linejoin="round" />
									</svg>
								</div>
							{/if}
						</div>
						{#if !isValid}
							<div id="{setting.id}-validation-error" class="flex flex-col">
								{#each validationResults as message}
									<p class="text-sm text-red-600">
										{message}
									</p>
								{/each}
							</div>
						{/if}
					</div>
				{/if}

				<!-- Toggle button -->
				{#if setting.type === 'checkbox'}
					<div class="flex flex-col space-y-3">
						<div class="flex flex-col items-left space-x-3">
							<!-- Enabled: "bg-blue-gv", Not Enabled: "bg-gray-200" -->
							<button
								type="button"
								role="switch"
								id="{setting.id}-input"
								aria-checked="true"
								aria-labelledby="{setting.id}-label"
								aria-describedby="{setting.id}-description"
								class="{setting.value  ? 'bg-blue-gv': 'bg-gray-200'} {!isValid ? 'border-red-600' : 'focus:ring-gray-900'} relative inline-flex flex-shrink-0 h-5 w-9 border-2 border-transparent rounded-full cursor-pointer transition-colors ease-in-out duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2"
								{...setting.properties}
								on:click={() => handleChange( !setting.value )}
							>
								<!-- Enabled: "translate-x-4", Not Enabled: "translate-x-0" -->
								<span aria-hidden="true" class="{setting.value  ? 'translate-x-4': 'translate-x-0'} inline-block h-4 w-4 rounded-full bg-white shadow transform ring-0 transition ease-in-out duration-200"></span>
							</button>
						</div>
						{#if !isValid}
							<div id="{setting.id}-validation-error" class="flex flex-col">
								{#each validationResults as message}
									<p class="text-sm text-red-600">
										{message}
									</p>
								{/each}
							</div>
						{/if}
					</div>
				{/if}

				<!-- Multiple checkboxes -->
				{#if setting.type === 'multiple_checkboxes'}
					<div class="flex flex-col space-y-3">
						<div class="flex flex-col items-left">
							{#if setting.selectAll === true}
								<div>
									<input
										id="{setting.id}-select-all"
										class="form-check-input appearance-none h-4 w-4 border border-gray-300 rounded-sm bg-white checked:bg-blue-600 checked:border-blue-600 focus:outline-none transition duration-200 mt-1 align-top bg-no-repeat bg-center bg-contain float-left mr-2 cursor-pointer"
										type="checkbox"
										value="select-all"
										aria-labelledby="{setting.id}-label"
										on:change={(e) => selectAllCheckboxes( e.target.checked )}
									>
									<label for="{setting.id}-select-all" class="inline-block text-sm text-gray-500">{ __( 'Select All', 'gk-foundation' ) }</label>
								</div>
							{/if}
							{#each setting.choices as choice}
								<div>
									<input
										id="{choice.title}"
										class="form-check-input appearance-none h-4 w-4 border border-gray-300 rounded-sm bg-white checked:bg-blue-600 checked:border-blue-600 focus:outline-none transition duration-200 mt-1 align-top bg-no-repeat bg-center bg-contain float-left mr-2 cursor-pointer"
										type="checkbox"
										value="{choice.value}"
										aria-required="{setting.required ? 'true' : null}"
										aria-invalid="{!isValid ? 'true' : 'false'}"
										aria-describedby="{!isValid ? `${setting.id}-validation-error` : `${setting.id}-description`}"
										aria-labelledby="{setting.id}-label"
										name="{setting.id}"
										checked="{setting.value.includes(choice.value) ? 'checked' : null}"
										on:change={(e) => handleChange(e.target.value)}
									>
									<label for="{choice.title}" class="inline-block text-sm text-gray-500">{@html sanitizeAndProcessMarkdown( choice.title )}</label>
								</div>
							{/each}
						</div>
						{#if !isValid}
							<div id="{setting.id}-validation-error" class="flex flex-col">
								{#each validationResults as message}
									<p class="text-sm text-red-600">
										{message}
									</p>
								{/each}
							</div>
						{/if}
					</div>
				{/if}

				<!-- Input field -->
				{#if [ 'text', 'number', 'email', 'password' ].includes( setting.type )}
					<div class="flex flex-col items-start space-y-3">
						<div class="relative w-full sm:w-56 items-start lg:w-72">
							<input
								type="{setting.type}"
								id="{setting.id}-input"
								name="{setting.id}"
								value="{setting.value}"
								placeholder="{setting.placeholder}"
								aria-required="{setting.required ? 'true' : null}"
								aria-invalid="{!isValid ? 'true' : 'false'}"
								aria-describedby="{!isValid ? `${setting.id}-validation-error` : `${setting.id}-description`}"
								aria-labelledby="{setting.id}-label"
								class="{!isValid ? 'border-red-600 placeholder-red-600 focus:ring-gray-900 focus:border-gray-900 text-red-600' : 'text-gray-900 focus:ring-gray-900 focus:border-gray-900 border-gray-300'} block w-full p-3 text-sm rounded-md"
								{...setting.properties}
								on:input={e => handleChange(e.target.value)}
							>
							{#if !isValid}
								<div class="absolute inset-y-0 right-0 {setting.type === 'number' ? 'pr-9' : 'pr-3'} flex items-center pointer-events-none text-red-600">
									<svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
										<path d="M7.99967 5.33325V7.99992M7.99967 10.6666H8.00634M14.6663 7.99992C14.6663 11.6818 11.6816 14.6666 7.99967 14.6666C4.31778 14.6666 1.33301 11.6818 1.33301 7.99992C1.33301 4.31802 4.31778 1.33325 7.99967 1.33325C11.6816 1.33325 14.6663 4.31802 14.6663 7.99992Z" stroke="currentColor" stroke-width="1.33333" stroke-linecap="round" stroke-linejoin="round" />
									</svg>
								</div>
							{/if}
						</div>
						{#if !isValid}
							<div id="{setting.id}-validation-error" class="flex flex-col">
								{#each validationResults as message}
									<p class="text-sm text-red-600">
										{message}
									</p>
								{/each}
							</div>
						{/if}
					</div>
				{/if}

				<!-- Text area -->
				{#if setting.type === 'textarea'}
					<div class="flex flex-col space-y-3">
					<textarea
						id="{setting.id}-input"
						name="{setting.id}"
						value="{setting.value}"
						placeholder="{setting.placeholder}"
						aria-required="{setting.required ? 'true' : null}"
						aria-invalid="{!isValid ? 'true' : 'false'}"
						aria-describedby="{!isValid ? `${setting.id}-validation-error` : `${setting.id}-description`}"
						aria-labelledby="{setting.id}-label"
						rows="5"
						class="{!isValid ? 'text-red-600 border-red-600 placeholder-red-600 focus:ring-gray-900 focus:border-gray-900' : 'text-gray-900 border-gray-300 focus:ring-gray-900 focus:border-gray-900'} w-full sm:w-56 p-3 text-sm rounded-md lg:w-72"
						{...setting.properties}
						on:input={e => handleChange(e.target.value )}
					></textarea>
						{#if !isValid}
							<div id="{setting.id}-validation-error" class="flex flex-col">
								{#each validationResults as message}
									<p class="text-sm text-red-600">
										{message}
									</p>
								{/each}
							</div>
						{/if}
					</div>
				{/if}
			</div>
		{/if}
	</div>
{/if}
