import './app.css';
import App from './App.svelte';

document.addEventListener( 'DOMContentLoaded', () => {
	const $el = document.getElementById( 'wpbody' );

	if ( !$el ) {
		console.error( 'GravityKit Settings could not initialize - target element not found.' );

		return;
	}

	if ( !window.gkSettings?.data ) {
		console.error( 'GravityKit Settings could not initialize - settings data is not available.' );

		return;
	}

	$el.innerHTML = '';

	window.gkSettings.__ = window.wp?.i18n?.__ || function ( translation ) { return translation; };
	window.gkSettings._hooks = window?.wp?.hooks ? window.wp.hooks.createHooks() : null;

	new App( {
		target: $el,
		props: {
			data: window.gkSettings.data
		}
	} );
} );
